.project-card {
  margin: auto;
  margin: 0px 10px 50px 10px;
  width: 45%;
  min-width: 300px;
  padding-top: 0px;
  background-color: #333;
  border-radius: 20px;
  box-shadow: 5px 5px 10px #111;
  overflow: hidden;
  flex-grow: 1;
}
.project-card-header {
  width: 100%;
  color: #ccc;
}
.project-card-title {
  height: 100px;
  text-align: center;
  background-color: #222;
  font-size: 20pt;
  line-height: 100px;
}

.project-card-info {
  padding: 20px;
  width: 100%;
  overflow: hidden;
}
.project-card-info-title {
  color: red;
  font-size: 20pt;
  font-weight: bold;
}
.project-card-info p {
  word-wrap: wrap;
  margin-right: 40px;
  padding: 0px;
}
.project-card-slideshow {
  width: 70%;
  margin-bottom: 50px;
}

.project-card-slideshow-nav {
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #aaa;
}
.project-card-slideshow-nav button {
  width: 60px;
  height: 60px;
  background-color: #444;
  color: white;
  border: none;
}
#slideId {
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 20pt;
  line-height: 60px;
  color: #1f3044;
}
.project-slide {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.project-slide-image {
  width: 60%;
  min-width: 300px;
  flex-grow: 1;
}
.project-slide-image img {
  width: 100%;
  height: 100%;
}

.project-slide-info {
  width: 20%;
  min-width: 100px;
  min-height: 100%;
  max-height: 600px;
  background-color: #1f3044;
  color: #ccc;
  overflow: scroll;
  flex-grow: 1;
}
.project-slide-info h2 {
  margin: 50px 0px 50px 20px;
  color: #fb9039;
}
.project-slide-info p {
  padding: 20px;
  line-height: 50px;
  font-size: 18pt;
}
@media only screen and (max-width: 700px) {
  .project-card-slideshow {
    width: 100%;
  }
  .project-slide-info h2 {
    margin-bottom: 0px;
  }
  .project-slide-info p {
    font-size: 14pt;
    line-height: 30px;
    margin-top: 0px;
  }
}
