/* 
  Main Color Scheme
  Dark Blue: #1F3044
  Orange: #FB9039
  Gray #646C79
*/

.container {
  display: flex;
  flex-flow: row-reverse wrap-reverse;
  justify-content: space-evenly;
  width: 90%;
  margin: auto;
  margin-top: 200px;
}
.header {
  background-color: #fb9039;
  flex-shrink: 3;
  width: 20%;
  min-width: 250px;
  max-width: 300px;
  /* background-repeat: y; */
  padding: 20px;
  box-shadow: 5px 5px 10px black;
  margin: 10px;
}
.content {
  width: 60%;
  margin: 10px;
  min-width: 100px;
  /* margin-top: 100px; */
  flex-grow: 1;
}
.content-title {
  font-size: 30px;
  color: #fb9039;
  margin: 10px;
}
a {
  text-decoration: none;
  color: #fb9039;
}
a:hover {
  cursor: pointer;
}

#welcome {
  margin-bottom: 30px;
}
.content-topic {
  font-size: 20px;
  color: #fb9039;
  margin-left: 10px;
}
.content-topic-desc {
  font-size: 18pt;
  line-height: 70px;
  margin: -10px 10px 30px 10px;
  color: white;
}
.content-subtitle {
  margin: 10px;
  margin-bottom: 50px;
}

@media only screen and (max-width: 500px) {
  .header {
    width: 60%;
    /* max-width: 200px; */
    padding: 20px;
  }
  .container {
    margin-top: 100px;
    width: 80%;
    justify-content: space-evenly;
  }
  .content {
    margin: 20px 0px 10px 0px;
  }
  .content-title {
    font-size: 20pt;
  }
  .content-topic {
    font-size: 14pt;
  }
  .content p {
    font-size: 14pt;
    line-height: 30px;
  }
}
@media only screen and (max-width: 950px) {
  .header {
    margin-top: 20px;
    width: 100%;
    max-width: 500px;
  }
}
